    },
    /**
     * @description: ztree
     * @param event 
     * @param nodes 
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // nameid
      this.searchIdArr = [];
      var nameUrl = [];
      nameUrl.push(nodes.name);
      var node = nodes.getParentNode();
      while (node) {
        nameUrl.push(node.name);
        node = node.getParentNode();
      }
      this.HeadTitleUrl = nameUrl.reverse().join(' / ');
      this.searchIdArr = [nodes.id];

      // 
      this.ztreeSelectedNode = nodes;
      // ztree
      this.ztreeObj = obj;
      // 
      this.tablePage.currentPage = 1;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id && this.ztreeSelectedNode.id !== '-1') {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        this.ztreeObj.selectNode(nodes[0]);
        this.ztreeObj.expandNode(nodes[0], true);
      }
      this.findList();
    },
    /**
     * @description: table
     * @param currentPage 
     * @param pageSize 
     * @return: 
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // table
      this.tablePage.currentPage = currentPage;
      // table
      this.tablePage.pageSize = pageSize;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param val
     * @return: 
     * @author: ZJM
     */
    async findList() {
      this.tableDatas = [];
      this.tablePage.total = 0;
      try {
        // table
        this.tableLoading = true;

        var deviceCode = '';
        if (this.$store.state.materialMkis === 1) {
          deviceCode = this.$store.state.materialRepair.deviceCode;
        } else {
          deviceCode = this.$store.state.materialMaintance.deviceCode;
        }

        const res = await sapInteractControllerGetDevicePartByDevicePriCode({
          deviceCode: deviceCode
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 
          this.tableLoading = false;
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.tableLoading = false;
        // showErrorNotify(TITLE.NOTIFY, '');
      } finally {
      }
    },
    async saveAction() {
      var deviceCode = '';
      var relationshipId = 0;
      var relationshipType = 0;
      if (this.$store.state.materialMkis === 1) { // 
        relationshipType = 1;
        deviceCode = this.$store.state.materialRepair.deviceCode;
        relationshipId = this.$store.state.materialRepair.repairTaskId;
      } else { // 
        relationshipType = 2;
        deviceCode = this.$store.state.materialMaintance.deviceCode;
        relationshipId = this.$store.state.materialMaintance.maintainId;
      }

      var paramJson = {
        deviceCode: deviceCode,
        partsPickingMaterialsList: [],
        relationshipId: relationshipId,
        relationshipType: relationshipType,
        remark: ''
      };
      console.log(paramJson);
      console.log(this.tableDatas);
      var jsonArr = [];
      for (var i = 0; i < this.tableDatas.length; i++) {
        if (this.tableDatas[i].sqNum && !isNaN(Number(this.tableDatas[i].sqNum))) {
          if (Number(this.tableDatas[i].sqNum) > 0 && Number(this.tableDatas[i].sqNum) <= Number(this.tableDatas[i].avlst)) {
            jsonArr.push({
              deviceCode: deviceCode,
              materialsCode: this.tableDatas[i].idnrk, // 
              materialsName: this.tableDatas[i].ktext, // 
              needCount: this.tableDatas[i].sqNum, // 
              unit: this.tableDatas[i].meins, // 
              specifications: this.tableDatas[i].posnr // 
            });
          }
        }
      }

      if (jsonArr.length === 0) {
        showWarningMessage('');
        return false;
      } else {
        paramJson.partsPickingMaterialsList = jsonArr;
      }

      try {
        const res = await partsPickingCreatePartPinking({}, paramJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.closeAction();
        } else {
          // 
          showWarningMessage(res.dataset);
        }
      } catch (error) {
        this.tableLoading = false;
        // showErrorNotify(TITLE.NOTIFY, '');
      } finally {
      }
    },
    /**
     * @description: 
     * @param name name
     * @return:
     * @author: ZJM
     */
    async togglePage(name) {
      // 
      if (name === 'addDevice') {
        // 
        this.showPage = 1;
      }
    },
    print(val) {
      this.$print(this.$refs.printQr);
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    groupConfig() {
      this.showPage = 3;
    },
    /**
     * @description: 
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(0, this.tableColumns_old.length - 1);
      this.tableColumns_operation = this.tableColumns_old[this.tableColumns_old.length - 1];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      this.tableColumns = this.tableColumns.concat(this.tableColumns_operation);
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: cyb
    */
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
      if (!this.isSearchBoxShow) {
        this.onTablePageChange(1, this.tablePage.pageSize);
      }
    },
    refreshTable() {
      this.findList();
    },
    dbclickRows(row) {
      this.$emit('dbclickRows', row);
    }
  }
};
</script>

